/*
 datePicker.js
 description:滑动选取日期（年，月，日,时间）
 vaersion:1.0
 author:ddd
 https://github.com/ddd702/datePicker
 update：2015-5-5(with iscroll4)
 */
;(function($) {
    "use strict";
    $.fn.datePicker = function(options) {
        return this.each(function(e) {
            //插件默认选项
            var that = $(this),
                docType = $(this).is('input'),
                nowdate = new Date(),
                yearScroll = null,
                monthScroll = null,
                dayScroll = null,
                hourScroll = null,
                minuteScroll = null,
                initY = null,
                initM = null,
                initD = null,
                initH = null,
                initI = null,
                initS = null,
                initVal = null;
            /*使用到的全局函数-e*/
            $.fn.datePicker.defaultOptions = {
                beginyear: 2010, //日期--年--份开始
                endyear: 2020, //日期--年--份结束
                monthDay: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31], //日期--12个月天数(默认2月是28,闰年为29)--份结束
                days: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
                beginhour: 0,
                endhour: 23,
                beginminute: 0,
                endminute: 59,
                curdate: false, //打开日期是否定位到当前日期
                liH: 40,
                theme: "date", //控件样式（1：日期(date)，2：日期+时间(datetime),3:时间(time),4:年月(month)）
                mode: null, //操作模式（滑动模式）
                event: "click", //打开日期插件默认方式为点击后后弹出日期
                show: true,
                startDate:new Date(new Date().getFullYear() , 2, 31) ||'',
                endDate:new Date(new Date().getFullYear() , 3, 2),
                currentValue:new Date(new Date().getFullYear() , 3,1,8) ||'',
                scrollOpt: {
                    snap: "li",
                    checkDOMChanges: true,
                    vScrollbar:false,
                    hScrollbar:false,
                    hScroll:false
                },
                callBack: function() {}
            }

            if(!$.fn.datePicker.defaultOptions.startDate){

                $.fn.datePicker.defaultOptions.startDate=new Date()

            }
            var opts = $.extend(true, {}, $.fn.datePicker.defaultOptions, options);

            opts.beginyear =getYear(opts.startDate),
                opts.endyear =getYear(opts.endDate);





            if (!opts.show) {
                that.off('click');
            } else {
                //绑定事件（默认事件为获取焦点）
                that.on(opts.event, function() {
                    init();
                });
            }
            function  isShortMonth(month) {
                return [4, 6, 9, 11].indexOf(month) > -1;
            }

            function  isDateString(str) {
                return /\d{4}(\-|\/|.)\d{1,2}\1\d{1,2}/.test(str);
            }
            function isLeapYear(year) {
                return (year % 400 === 0) || (year % 100 !== 0 && year % 4 === 0);
            }
            function  getYear(value) {
                if(isDateString(value) ){
                    var tm =  value.split(' ')[0].split(/-|\/|\./)[0];
                    return tm;
                }else{
                    return value.getFullYear();
                }
            }
            function  getMonth(value) {
                return isDateString(value) ? value.split(' ')[0].split(/-|\/|\./)[1] : value.getMonth() + 1;
            }

            function  getDate(value) {
                return isDateString(value) ? value.split(' ')[0].split(/-|\/|\./)[2] : value.getDate();
            }

            function getHour(value) {
                if (isDateString(value)) {
                    var str = value.split(' ')[1] || '00:00:00';
                    return str.split(':')[0];
                }
                return value.getHours();
            }

            function  getMinute(value) {
                if (isDateString(value)) {
                    var str = value.split(' ')[1] || '00:00:00';
                    return str.split(':')[1];
                }
                return value.getMinutes();
            }

            function getMonthEndDay(year, month) {
                if (isShortMonth(month)) {
                    return 30;
                } else if (month === 2) {
                    return isLeapYear(year) ? 29 : 28;
                } else {
                    return 31;
                }
            }
            var result = {
                year: [opts.startDate.getFullYear(), opts.endDate.getFullYear()],
                month: [1, 12],
                date: [1, getMonthEndDay(getYear(opts.currentValue),getMonth(opts.currentValue))],
                hour: [0, 23],
                min: [0, 59]
            };

            function initdistancs(){
                result = {
                    year: [opts.startDate.getFullYear(), opts.endDate.getFullYear()],
                    month: [1, 12],
                    date: [1, getMonthEndDay(getYear(opts.currentValue),getMonth(opts.currentValue))],
                    hour: [0, 23],
                    min: [0, 59]
                };
                return  result;
            }
            function  rimDetect(result, rim) {
                let position = rim === 'start' ? 0 : 1;
                let rimDate = rim === 'start' ? opts.startDate : opts.endDate;
                if (getYear(opts.currentValue) == rimDate.getFullYear()) {
                    result.month[position] = rimDate.getMonth() + 1;

                    if (('0' +getMonth(opts.currentValue)).slice(-2)  == ('0' +(rimDate.getMonth() + 1)).slice(-2)) {


                        result.date[position] = rimDate.getDate();

                        if (('0' +getDate(opts.currentValue)).slice(-2)== ('0' +rimDate.getDate()).slice(-2)) {
                            result.hour[position] = rimDate.getHours();
                            if (('0' +getHour(opts.currentValue)).slice(-2) == ('0' +rimDate.getHours()).slice(-2)) {
                                result.min[position] = rimDate.getMinutes();
                            }
                        }
                    }
                }
            }



            function init() { //初始化函数
                initVal = that.val();
                if (!$('#datePlugin').size()) {
                    $('body').append('<div id="datePlugin"></div>');
                }
                rimDetect(result, 'start'), rimDetect(result, 'end');


                document.getElementsByTagName('body')[0].addEventListener('touchmove', cancleDefault, false);
                if (!opts.curdate && $.trim(initVal) != '') {
                    var inputDate = null,
                        inputTime = null;
                    if (opts.theme == 'date' || opts.theme == 'datetime') {
                        inputDate = initVal.split(' ')[0];
                        initY = parseInt(inputDate.split('-')[0] - parseInt(opts.beginyear)) + 1,
                            initM = parseInt(inputDate.split('-')[1]),
                            initD = parseInt(inputDate.split('-')[2]);
                    }
                    if (opts.theme == 'datetime') {
                        inputTime = initVal.split(' ')[1];
                        initH = parseInt(inputTime.split(':')[0]) + 1,
                            initI = parseInt(inputTime.split(':')[1]) + 1;
                    }
                    if (opts.theme == 'time') {
                        inputTime = initVal;
                        initH = parseInt(inputTime.split(':')[0]) + 1,
                            initI = parseInt(inputTime.split(':')[1]) + 1;
                    }
                    if (opts.theme == 'month') {
                        inputDate = initVal;
                        initY = parseInt(inputDate.split('-')[0] - parseInt(opts.beginyear)) + 1,
                            initM = parseInt(inputDate.split('-')[1]);
                    }
                } else {
                    //定位到当前日期
                    initY = parseInt(nowdate.getFullYear()) - parseInt(opts.beginyear) + 1,
                        initM = parseInt(nowdate.getMonth()) + 1,
                        initD = parseInt(nowdate.getDate()),
                        initH = parseInt(nowdate.getHours()) + 1,
                        initI = parseInt(nowdate.getMinutes()) + 1,
                        initS = parseInt(nowdate.getSeconds());
                }
                $('#datePlugin').show();
                destroyScroll();
                //     res =  initdistancs();
                renderDom();
                $('#d-okBtn').on('click', function(event) {
                    destroyScroll();
                    document.getElementsByTagName('body')[0].removeEventListener('touchmove', cancleDefault, false);
                    var y = $('#yearScroll li').eq(initY).data('num');
                    var M = $('#monthScroll li').eq(initM).data('num');
                    var d = $('#dayScroll li').eq(initD).data('num');
                    var h = $('#hourScroll li').eq(initH).data('num');
                    var m = $('#minuteScroll li').eq(initI).data('num');
                    that.val($('.d-return-info').html());
                    $('#datePlugin').hide().html('');
                    opts.callBack({y:y,M:M,d:d,h:h,m:m});
                });
                $('#d-cancleBtn').on('click', function(event) {
                    destroyScroll();
                    $('#datePlugin').hide().html('');
                    document.getElementsByTagName('body')[0].removeEventListener('touchmove', cancleDefault, false);
                });
            }

            function cancleDefault(event) {
                event.preventDefault();
            }

            function isLeap(y) {
                if ((y % 4 == 0 && y % 100 != 0) || y % 400 == 0) {
                    return true;
                } else {
                    return false;
                }
            }

            function renderDom() {

                var mainHtml = ' <div class="d-date-box"><div class="d-date-btns"><button class="d-btn" id="d-okBtn">确定</button><button class="d-btn" id="d-cancleBtn">取消</button></div><div class="d-date-title">请选择日期</div><div class="d-date-title-current"><span>您选择的是：</span><p class="d-date-info"><span class="d-day-info"></span><span class="d-return-info"></span></p></div></div>';
                var dateHtml = '<div class="d-date-wrap">';
                dateHtml += '<div class="d-date-mark"></div>';
                dateHtml += '<div class="d-year-wrap d-date-cell" style="display:none" id="yearScroll"><ul></ul></div>';
                dateHtml += '<div class="d-month-wrap d-date-cell" id="monthScroll"><ul></ul></div>';
                dateHtml += '<div class="d-day-wrap d-date-cell" id="dayScroll"><ul></ul></div>';
                dateHtml += '<div class="d-hour-wrap d-date-cell" id="hourScroll"><ul></ul></div>';
                dateHtml += '<div class="d-minute-wrap d-date-cell" id="minuteScroll"><ul></ul></div>';
                dateHtml += '</div>';

                var timeHtml = '<div  style="display:none" class="d-date-wrap d-time-wrap">';
                timeHtml += '<div class="d-date-mark"></div>';
                timeHtml += '</div>';
                var monthHtml = '<div  style="display:block" class="d-date-wrap">';
                monthHtml += '<div class="d-date-mark"></div>';
                monthHtml += '<div class="d-year-wrap d-date-cell" style="width:50%" id="yearScroll"><ul></ul></div>';
                monthHtml += '<div class="d-month-wrap d-date-cell" style="width:50%" id="monthScroll"><ul></ul></div>';
                monthHtml += '</div>';
                $('#datePlugin').html(mainHtml);
                switch (opts.theme) {
                    case 'date':
                        $('.d-date-box').append(dateHtml);
                        createYear();
                        createMonth();
                        createDay(opts.monthDay[initM - 1]);
                        break;
                    case 'datetime':
                        $('.d-date-box').append(dateHtml);
                        $('.d-date-box').append(timeHtml);
                        createYear();
                        createMonth();
                        createDay(opts.monthDay[initM - 1]);
                        createHour();
                        createMinute();
                        break;
                    case 'time':
                        $('.d-date-box').append(timeHtml);
                        createHour();
                        createMinute();
                        break;
                    case 'month':
                        $('.d-date-box').append(monthHtml);
                        createYear();
                        createMonth();
                        break;
                    default:
                        $('.d-date-box').append(dateHtml);
                        createYear();
                        createMonth();
                        createDay(opts.monthDay[initM - 1]);
                        break;
                }
                showTxt();
            }

            function showTxt() {
                var y = $('#yearScroll li').eq(initY).data('num')||'',
                    m = $('#monthScroll li').eq(initM).data('num')||'',
                    d = $('#dayScroll li').eq(initD).data('num')||'',
                    h = $('#hourScroll li').eq(initH).data('num')||'',
                    i = $('#minuteScroll li').eq(initI).data('num')||'';
                var tmpstr =y + '-' + m + '-' + d,
                    date = new Date(tmpstr.replace(/-/g, '/'));
                switch (opts.theme) {
                    case 'date':
                        $('.d-day-info').html(opts.days[date.getDay()] + "&nbsp;");
                        $('.d-return-info').html(y + '-' + m + '-' + d);
                        break;
                    case 'datetime':
                        $('.d-day-info').html(opts.days[date.getDay()] + "&nbsp;");
                        $('.d-return-info').html(y + '-' + m + '-' + d + ' ' + h + ':' + i);
                        break;
                    case 'time':
                        $('.d-return-info').html(h + ':' + i);
                        break;
                    case 'month':
                        $('.d-return-info').html(y + '-' + m);
                        break;
                    default:
                        $('.d-day-info').html(opts.days[date.getDay()] + "&nbsp;");
                        $('.d-return-info').html(y + '-' + m + '-' + d);
                        break;
                }
            }
            function destroyScroll(){//销毁iscroll滚动
                var scrollArr=[yearScroll,monthScroll,dayScroll,hourScroll,minuteScroll];
                scrollArr.forEach(function(itm){
                    if (itm!=null) {
                        itm.destroy();
                        itm=null;
                    }
                });
            }
            function createYear() {
                var yearDom = $('#yearScroll'),
                    yearNum = opts.endyear - opts.beginyear,
                    yearHtml = '<li></li>';
                var startyear =Number(result.year[0]);
                var endyear =Number(result.year[1]);
                if(startyear<=endyear && startyear>0){
                    for (var i = 0; i<=yearNum; i++) {
                        yearHtml += '<li data-num=' + (opts.beginyear + i) + '>' + (opts.beginyear + i) + '年</li>';
                    }
                }
                yearDom.find('ul').html(yearHtml).append('<li></li>');
                yearScroll = new iScroll('yearScroll', $.extend(true, {}, opts.scrollOpt, {
                    onScrollEnd: function() {
                        yearScrollEnd(this);
                    }
                }));
                yearScroll.scrollTo(0, -(initY - 1) * opts.liH);

                function yearScrollEnd(_this) {
                    var yIndex = Math.floor(-_this.y / opts.liH);
                    initY = yIndex + 1;
                    if (isLeap(parseInt(yearDom.find('li').eq(initY).data('num')))) {
                        opts.monthDay[1] = 29;
                    } else {
                        opts.monthDay[1] = 28;
                    }
                    if (initM == 2 && opts.theme != 'month') {
                        createDay(opts.monthDay[initM - 1]);
                    }
                    showTxt();
                };
            }

            function createMonth() {
                var monthDom = $('#monthScroll'), monthHtml = '<li></li>';
                var startmonth =Number(result.month[0]), endmonth =Number(result.month[1]);

                for (var i = startmonth; i <= endmonth; i++) {
                    if (i < 10) {
                        monthHtml += '<li data-num="0' + i + '">0' + i + '月</li>';
                    } else {
                        monthHtml += '<li data-num="' + i + '">' + i + '月</li>';
                    }
                };
                monthDom.find('ul').html(monthHtml).append('<li></li>');
                monthScroll = new iScroll('monthScroll', $.extend(true, {}, opts.scrollOpt, {
                    onScrollEnd: function() {
                        monthScrollEnd(this,startmonth);
                    }
                }));

                //定位当前月份

                monthScroll.scrollTo(0, 0);

                function monthScrollEnd(_this) {
                    var mIndex = Math.floor(-_this.y / opts.liH);
                    //获取一个月有多少天
                    var dayNum = opts.monthDay[mIndex];
                    initM = mIndex + 1;
                    if (opts.theme != 'month') {
                        createDay(dayNum);
                    }
                    showTxt();
                }
            }

            function createDay(dayNum) {
                var dayDom = $('#dayScroll'),
                    dayHtml = '<li></li>';

                //获取选择的月份去计算天数
                var m = $('#monthScroll li').eq(initM).data('num') ||'';

                var m_01=0;

                if(m && m.length==2){
                    m_01=m.substr(0, 1);
                    m_01 =Number(m_01);
                    if(m_01===0){
                        m_01=m.substr(1, 1);
                        m_01 =Number(m_01);
                    }
                }

                console.log(result)
                var y = $('#yearScroll li').eq(initY).data('num')
                var startmonth =Number(result.month[0]);
                var endmonth =Number(result.month[1]);
                var startdate =Number(result.date[0]);
                var starthour =Number(result.hour[0]);
                var enddate =Number(result.date[1]);
                var endtimecompute = getDate(opts.endDate);
                var startimecompute = getDate(opts.startDate);
                var endhourcompute = getHour(opts.endDate);
                var countday =	getMonthEndDay(y,m_01);

                if(startmonth ==endmonth){

                    //月份相等 后判断日期条件
                    if(('0' +((opts.startDate).getMonth() + 1)).slice(-2)== m){
                        for (var i = startimecompute; i <= endtimecompute; i++) {
                            if (i < 10) {
                                dayHtml += '<li data-num="0' + i + '">0' + i + '日</li>';
                            } else {
                                dayHtml += '<li data-num="' + i + '">' + i + '日</li>';
                            }
                        }
                    }
                }else if(startmonth !==endmonth && startmonth<endmonth){


                    //月份不相等 后判断日期条件  例如 月份 >3  小于 4
                    if(('0' +((opts.startDate).getMonth() + 1)).slice(-2)== m){


                        for (var i = startimecompute; i <= countday; i++) {
                            if (i < 10) {
                                dayHtml += '<li data-num="0' + i + '">0' + i + '日</li>';
                            } else {
                                dayHtml += '<li data-num="' + i + '">' + i + '日</li>';
                            }
                        };
                    }else if(('0' +((opts.endDate).getMonth() + 1)).slice(-2)> m){

                        for (var i = 1; i <= countday; i++) {
                            if (i < 10) {
                                dayHtml += '<li data-num="0' + i + '">0' + i + '日</li>';
                            } else {
                                dayHtml += '<li data-num="' + i + '">' + i + '日</li>';
                            }
                        };

                    }else if(('0' +((opts.endDate).getMonth() + 1)).slice(-2)==m){

                        for (var i = 1; i <= endtimecompute; i++) {
                            if (i < 10) {
                                dayHtml += '<li data-num="0' + i + '">0' + i + '日</li>';
                            } else {
                                dayHtml += '<li data-num="' + i + '">' + i + '日</li>';
                            }
                        };

                    }





                }






                dayDom.find('ul').html(dayHtml).append('<li></li>');
                dayScroll = new iScroll('dayScroll', $.extend(true, {}, opts.scrollOpt, {
                    onScrollEnd: function() {
                        dayScrollEnd(this);
                    }
                }));
                if (initD > opts.monthDay[initM - 1]) {
                    initD = 1;
                }
                dayScroll.scrollTo(0, -(1 - 1) * opts.liH);

                function dayScrollEnd(_this) {
                    initD = Math.floor(-_this.y / opts.liH) + 1;

                    showTxt();
                    createHour();
                }
            }

            function createHour() {
                var hourDom = $('#hourScroll'),
                    hourHtml = '<li></li>';
                var y = $('#yearScroll li').eq(initY).data('num'),
                    m = $('#monthScroll li').eq(initM).data('num'),
                    d = $('#dayScroll li').eq(initD).data('num'),
                    h = $('#hourScroll li').eq(initH).data('num'),
                    i = $('#minuteScroll li').eq(initI).data('num');

                var   ddd = $('#dayScroll li').eq(initD).data('num')||'00';//选择的时间 天

                if(ddd && ddd.length==2){

                    if(ddd.substr(0, 1)=='0'){
                        ddd=ddd.substr(1, 1);
                        ddd =Number(ddd);
                    }
                }


                var startmonth =Number(result.month[0]);
                var endmonth =Number(result.month[1]);
                var startdate =Number(result.date[0]);
                var starthour =Number(result.hour[0]);
                var enddate =Number(result.date[1]);
                var endtimecompute = getDate(opts.endDate);
                var startDatedaycompute = getDate(opts.startDate);
                var endhourcompute = getHour(opts.endDate);
                var startDatecompute = getHour(opts.startDate);


                if(startmonth ==endmonth){
                    //月份相等 后判断日期条件
                    if(startDatedaycompute== ddd){
                        for (var i = startDatecompute; i <= opts.endhour; i++) {
                            if (i < 10) {
                                hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                            } else {
                                hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                            }
                        }
                    }else  if(startDatedaycompute< ddd && ddd<endtimecompute ) {
                        for (var i = 1; i <= opts.endhour; i++) {
                            if (i < 10) {
                                hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                            } else {
                                hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                            }
                        }

                    }else if(endtimecompute== ddd){
                            for (var i = 1; i <= endhourcompute; i++) {
                                if (i < 10) {
                                    hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                                } else {
                                    hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                                }
                            }
                    }
                }else if(startmonth !==endmonth){
                    //月份不相等 后判断日期条件  例如 月份 >3  小于 4
                    if(('0' +startmonth).slice(-2) == m){

                        if(startDatedaycompute== ddd ){

                            for (var i = startDatecompute; i <= opts.endhour; i++) {
                                if (i < 10) {
                                    hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                                } else {
                                    hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                                }
                            }
                        }else{
                            for (var i = 1; i <=opts.endhour  ; i++) {
                                if (i < 10) {
                                    hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                                } else {
                                    hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                                }
                            }
                        }
                    }else if(('0' +endmonth).slice(-2)==m){

                        if( endtimecompute==ddd){
                            for (var i = 1; i <= endhourcompute; i++) {
                                if (i < 10) {
                                    hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                                } else {
                                    hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                                }
                            }
                        }else{
                            for (var i = 1; i <= opts.endhour; i++) {
                                if (i < 10) {
                                    hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                                } else {
                                    hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                                }
                            }
                        }
                    }else{

                        for (var i = 1; i <= opts.endhour; i++) {
                            if (i < 10) {
                                hourHtml += '<li data-num="0' + i + '">0' + i + '时</li>';
                            } else {
                                hourHtml += '<li data-num="' + i + '">' + i + '时</li>';
                            }
                        }


                    }
                }

                hourDom.find('ul').html(hourHtml).append('<li></li>');
                hourScroll = new iScroll('hourScroll',  $.extend(true, {}, opts.scrollOpt, {
                    onScrollEnd: function() {
                        hourScrollEnd(this);
                    }
                }));
                hourScroll.scrollTo(0, -(1 - 1) * opts.liH);
                function hourScrollEnd(_this) {
                    initH = Math.floor(-_this.y / opts.liH) + 1;
                    showTxt();
                }
            }

            function createMinute() {
                var minuteDom = $('#minuteScroll'),
                    minuteHtml = '<li></li>';
                minuteDom.css('display','none');

                let startmonth =Number(result.min[0]);
                let endmonth =Number(result.min[1]);
                if(startmonth >0 && endmonth>startmonth){
                    for (var i = 0; i <= 59; i++) {
                        if (i < 10) {
                            minuteHtml += '<li data-num="0' + i + '">0' + i + '分</li>';
                        } else {
                            minuteHtml += '<li data-num="' + i + '">' + i + '分</li>';
                        }
                    };
                }else{
                    for (var i = 0; i <= 59; i++) {
                        if (i < 10) {
                            minuteHtml += '<li data-num="0' + i + '">0' + i + '分</li>';
                        } else {
                            minuteHtml += '<li data-num="' + i + '">' + i + '分</li>';
                        }
                    };
                }


                minuteDom.find('ul').html(minuteHtml).append('<li></li>');
                minuteScroll = new iScroll('minuteScroll', $.extend(true, {}, opts.scrollOpt, {
                    onScrollEnd: function() {
                        minuteScrollEnd(this);
                    }
                }));
                minuteScroll.scrollTo(0, -(initI - 1) * opts.liH);
                function minuteScrollEnd(_this) {
                    initI = Math.floor(-_this.y / opts.liH) + 1;
                    showTxt();
                }
            }
        });
    }
})(typeof(Zepto) != 'undefined' ? Zepto : jQuery);


